const initialState = {
  entriesPerPage: 10,
  pageNumber: 1,
  totalPages: 1,
  totalEntries: 10
};

const pageOutputReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_PAGE_OUTPUT":
      return action.data;
    default:
      return state;
  }
};

export default pageOutputReducer;
