import loginService from "../services/login";
import userService from "../services/user";

function initState() {
  return {
    token: localStorage.token,
    firstName: localStorage.firstName,
    id: localStorage.id,
    favorites: localStorage.favorites,
    username: localStorage.username
  };
}

const loginReducer = (state = initState(), action) => {
  switch (action.type) {
    case "SET_USER":
      if (action.data) userService.setToken(action.data.token);
      return action.data;
    default:
      return state;
  }
};

export const fetchUser = () => {
  return dispatch => {
    const userStr = window.localStorage.getItem("loggedVintageUser");
    const user = JSON.parse(userStr);
    if (user) {
      dispatch({ type: "SET_USER", data: user });
    }
  };
};

export const setUser = data => {
  return dispatch => {
    dispatch({ type: "SET_USER", data });
  };
};

export const login = data => {
  return async dispatch => {
    const user = await loginService.login({
      username: data.username,
      password: data.password
    });
    window.localStorage.setItem("loggedVintageUser", JSON.stringify(user));
    dispatch({ type: "SET_USER", data: user });
  };
};

export const logout = () => {
  return dispatch => {
    window.localStorage.removeItem("loggedVintageUser");
    dispatch({ type: "SET_USER", data: initState() });
  };
};

export default loginReducer;
