export const queryChange = query => {
  return {
    type: "SET_QUERY",
    query
  };
};

const queryReducer = (state = "t206", action) => {
  switch (action.type) {
    case "SET_QUERY":
      return action.query;
    case "RESET_QUERY":
      return null;
    default:
      return state;
  }
};

export default queryReducer;
