import React from "react";
import Footer from "./Footer";
import MenuNav from "./MenuNav";
import { Container, Header } from "semantic-ui-react";
import { Link } from "react-router-dom";

const About = props => {
  return (
    <>
      <MenuNav />
      <Container textAlign="left" text style={{ marginTop: "1em" }}>
        <Header as="h1">About Us</Header>
        <p>
          All Vintage Search got started as a side project after launching the
          site{" "}
          <a
            href="http://www.allvintagecards.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            All Vintage Cards
          </a>
          . I became frustrated with the search abilities on eBay and wanted a
          solution to identify the most watched vintage cards on eBay.
        </p>
        <p>Thus, out of my frustrations, All Vintage Search was born!</p>
        <p>
          The website for now, is desktop focused, and won't work very well on
          mobile. I hope to come up with a mobile friendly site or app in the
          future, but for now, i wanted an optimized desktop experience.
        </p>
        <p>
          I will also in the future provide a help video to show exactly how to
          use the site. I do think it is mostly self explanatory, but think a
          video might help. Ultimately, use this site to store your favorite
          cards. Of course you can do this on eBay as well, but this ultimately
          could just become another tool for collectors looking for a good deal.
        </p>
        <p>
          Currently the site is Baseball focused, but if the desire is there, i
          will expand to other sports. Also, I only selected what I thought were
          the most popular players and sets but if you think something should be
          added let me know. I can be reached at &nbsp;
          <a href="mailto:chris@allvintagecards.com">
            chris@allvintagecards.com
          </a>
          . &nbsp;In addition if you notice any bugs or have any other ideas for
          improvement, let me know! I hope you enjoy the site!
        </p>
      </Container>
      <Footer />
    </>
  );
};

export default About;
