//     headers: { Authorization: token }
//   };
//   const request = axios.delete(`${baseUrl}/${blog.id}`, config);
//   return request.then(response => response.data);
// };

const update = async (id, card) => {
  const response = await axios.put(`${baseUrl}/${id}`, card);
  return response.data;
};

const removeFav = async (id, card) => {
  const response = await axios.put(`${baseUrl}/${id}/removefav`, card);
  return response.data;
};

export default { getAll, getFavorites, create, update, removeFav, setToken };
