import dataService from "../services/dataUpdate";

const updateReducer = (state = "", action) => {
  switch (action.type) {
    case "GET_DATA":
      return action.data;
    default:
      return state;
  }
};

export const getUpdates = id => {
  return async dispatch => {
    const updates = await dataService.getMultiple(id);
    dispatch({
      type: "GET_DATA",
      data: updates
    });
  };
};
export default updateReducer;
