import baseballService from "../services/baseball";

const baseballReducer = (state = [], action) => {
  switch (action.type) {
    case "INIT_BLOGS":
      return action.data;
    case "UPDATE_DATA":
      return action.data;
    case "QUERY_DATA":
      return action.data;
    default:
      return state;
  }
};

export const initializeBaseball = () => {
  return async dispatch => {
    const baseballCards = await baseballService.getAll();
    dispatch({
      type: "INIT_BLOGS",
      data: JSON.parse(baseballCards.data)
    });
    dispatch({
      type: "GET_PAGE_OUTPUT",
      data: JSON.parse(baseballCards.pageOutput)
    });
  };
};

export const fetchData = (query, page, entries) => {
  return async dispatch => {
    const queryBaseball = await baseballService.getByQuery(
      query,
      page,
      entries
    );
    dispatch({
      type: "QUERY_DATA",
      data: JSON.parse(queryBaseball.data)
    });
    dispatch({
      type: "GET_PAGE_OUTPUT",
      data: JSON.parse(queryBaseball.pageOutput)
    });
  };
};

export default baseballReducer;
