import React from "react";
import { Container, Image, Segment, List } from "semantic-ui-react";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <Segment>
      <Container textAlign="center">
        <Link to="/">
          <Image
            centered
            size="medium"
            src={`${process.env.PUBLIC_URL}/assets/images/all-vintage-search-logo.png`}
          />
        </Link>
        <List horizontal divided link size="small">
          <List.Item as="a" href="/about">
            About
          </List.Item>
          <List.Item as="a" href="/contact">
            Contact Us
          </List.Item>
          <List.Item as="a" href="/terms">
            Terms and Conditions
          </List.Item>
          <List.Item as="a" href="/privacy">
            Privacy Policy
          </List.Item>
          <List.Item as="a" href="/affiliate">
            Affiliate Disclosure
          </List.Item>
        </List>
      </Container>
    </Segment>
  );
};

export default Footer;
