import React from "react";
import Footer from "./Footer";
import MenuNav from "./MenuNav";
import { Container, Header } from "semantic-ui-react";

const Contact = props => {
  return (
    <>
      <MenuNav />
      <Container textAlign="left" text style={{ marginTop: "1em" }}>
        <Header as="h1">Contact Us</Header>
        <p>
          Feel free to reach with any feedback or questions at &nbsp;
          <a href="mailto:chris@allvintagecards.com">
            chris@allvintagecards.com
          </a>
        </p>
      </Container>
      <Footer />
    </>
  );
};

export default Contact;
