import userService from "../services/user";

const favoriteReducer = (state = "", action) => {
  switch (action.type) {
    case "GET_FAVORITES":
      return action.data;
    case "REMOVE_FAVORITE":
      const userToRemoveFav = action.data.user;
      console.log("Card", action.data.card);
      console.log("User", action.data.user);
      console.log(
        "State",
        state.filter(item => item.id !== action.data.card.id)
      );
      userService.removeFav(userToRemoveFav.id, action.data.card);
      return state.filter(item => item.id[0] !== action.data.card.id);
    default:
      return state;
  }
};

export const getFavorites = id => {
  return async dispatch => {
    const favorites = await userService.getFavorites(id);
    dispatch({
      type: "GET_FAVORITES",
      data: favorites
    });
  };
};

export const removeFavorite = (card, user) => {
  return async dispatch => {
    dispatch({
      type: "REMOVE_FAVORITE",
      data: {
        card: card,
        user: user
      }
    });
  };
};

export default favoriteReducer;
