import React from "react";
import { connect } from "react-redux";
import { fetchData } from "../reducers/baseballReducer";
import { entriesChange } from "../reducers/entryReducer";
import { Dropdown } from "semantic-ui-react";

const limitOptions = [
  { key: "0", value: "10", text: "10" },
  { key: "1", value: "25", text: "25" },
  { key: "2", value: "50", text: "50" },
  { key: "3", value: "100", text: "100" }
];

const CardPageSizeSelect = props => {
  const onChangeEntries = (event, data) => {
    props.entriesChange(data.value);
    props.fetchData(props.query, props.page, data.value);
  };

  return (
    <React.Fragment>
      Records per page:{" "}
      <Dropdown
        inline
        options={limitOptions}
        defaultValue={props.entries}
        onChange={onChangeEntries}
      />
    </React.Fragment>
  );
};

const mapStateToProps = state => {
  return {
    query: state.query,
    entries: state.entries,
    page: state.page
  };
};

const mapDispatchToProps = {
  entriesChange,
  fetchData
};

export default connect(mapStateToProps, mapDispatchToProps)(CardPageSizeSelect);
