import React, { useEffect } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { BrowserRouter as Router, Route, withRouter } from "react-router-dom";
import { Container } from "semantic-ui-react";
import Cards from "./components/Cards";
import About from "./components/About";
import Terms from "./components/Terms";
import Affiliate from "./components/Affiliate";
import Privacy from "./components/Privacy";
import Contact from "./components/Contact";
import Notification from "./components/Notification";
import Favorites from "./components/Favorites";
import MenuNav from "./components/MenuNav";
import Login from "./components/Login";
import NewUser from "./components/NewUser";
import { initializeBaseball } from "./reducers/baseballReducer";
import { fetchUser, setUser } from "./reducers/loginReducer";
import userService from "./services/user";

// const blogFormRef = React.createRef();

const App = props => {
  const loadBaseball = props.initializeBaseball;

  useEffect(() => {
    loadBaseball();
  }, [loadBaseball]);

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem("loggedVintageUser");
    console.log("LOGGED USER", loggedUserJSON);
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);
      props.setUser(user);
      userService.setToken(user.token);
    }
  }, []);

  // const blogById = id => props.blogs.find(a => a.id === id);
  // const userById = id => props.users.find(a => a.id === id);

  return (
    <Container>
      <Notification />
      <Router>
        <Route exact path="/">
          <MenuNav />
          <Cards />
        </Route>
        <Route exact path="/login">
          <Login />
        </Route>
        <Route exact path="/about">
          <About />
        </Route>
        <Route exact path="/contact">
          <Contact />
        </Route>
        <Route exact path="/privacy">
          <Privacy />
        </Route>
        <Route exact path="/terms">
          <Terms />
        </Route>
        <Route exact path="/affiliate">
          <Affiliate />
        </Route>
        <Route exact path="/favorites">
          {props.loggedUser.id !== undefined ? (
            <>
              <MenuNav />
              <Favorites />
            </>
          ) : (
            <>
              <MenuNav />
              <Cards />
            </>
          )}
        </Route>
        <Route exact path="/newuser">
          <NewUser />
        </Route>
      </Router>
      <div>
        <br />
        {/* <em>Blog app, Chris Stuart 2019</em> */}
      </div>
    </Container>
  );
};

const mapStateToProps = state => {
  return {
    baseball: state.baseball,
    loggedUser: state.loggedUser
  };
};

const mapDispatchToProps = {
  initializeBaseball,
  fetchUser,
  setUser
};

App.propTypes = {
  baseball: PropTypes.array
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
