import { createStore, combineReducers, applyMiddleware } from "redux";
import baseballReducer from "./reducers/baseballReducer";
import searchReducer from "./reducers/searchReducer";
// import filterReducer from "./reducers/filterReducer";
// import columnReducer from "./reducers/columnReducer";
import pageReducer from "./reducers/pageReducer";
import pageOutputReducer from "./reducers/pageOutputReducer";
import entryReducer from "./reducers/entryReducer";
import queryReducer from "./reducers/queryReducer";
// import directionReducer from "./reducers/directionReducer";
import loginReducer from "./reducers/loginReducer";
import userReducer from "./reducers/userReducer";
import notificationReducer from "./reducers/notificationReducer";
import favoriteReducer from "./reducers/favoriteReducer";
import updateReducer from "./reducers/updateReducer";

import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";

const reducer = combineReducers({
  baseball: baseballReducer,
  entries: entryReducer,
  page: pageReducer,
  pageOutput: pageOutputReducer,
  search: searchReducer,
  query: queryReducer,
  // filteredData: filterReducer,
  // column: columnReducer,
  // direction: directionReducer,
  loggedUser: loginReducer,
  users: userReducer,
  notification: notificationReducer,
  favorites: favoriteReducer,
  favUpdates: updateReducer
});

const store = createStore(reducer, composeWithDevTools(applyMiddleware(thunk)));

export default store;
