import React from "react";
import { connect } from "react-redux";
import { createUser } from "../reducers/userReducer";
import Footer from "./Footer";
import MenuNav from "./MenuNav";
import { withRouter } from "react-router-dom";
import { login } from "../reducers/loginReducer";
import { setNotification } from "../reducers/notificationReducer";
import { Form, Segment } from "semantic-ui-react";
import { useField } from "../hooks";

const NewUser = props => {
  const newFirstName = useField("text");
  const newEmail = useField("text");
  const newUserName = useField("text");
  const newPassword = useField("text");

  const addUser = async event => {
    try {
      event.preventDefault();

      const userObject = {
        firstName: newFirstName.value,
        email: newEmail.value,
        username: newUserName.value,
        password: newPassword.value
      };

      const loginUser = {
        username: newUserName.value,
        password: newPassword.value
      };

      await props.createUser(userObject);
      await props.login(loginUser);
      //   newName.reset();
      //   newUserName.reset();
      //   newPassword.reset();
      props.history.push("/");
      props.setNotification(`You've added a new account!`, 4);
    } catch (error) {
      props.setNotification(
        `Sorry can't create user.  Here's why: ` + error.response.data.error,
        4
      );
    }
  };

  return (
    <>
      <MenuNav />
      <Segment padded>
        <h1>Create an All Vintage Search Account</h1>
        <Form onSubmit={addUser}>
          <Form.Field>
            <Form.Input {...newFirstName} reset={null} label="First Name" />
          </Form.Field>
          <Form.Field>
            <Form.Input {...newEmail} reset={null} label="Email" />
          </Form.Field>
          <Form.Field>
            <Form.Input {...newUserName} reset={null} label="Username" />
          </Form.Field>
          <Form.Field>
            <Form.Input {...newPassword} reset={null} label="Password" />
          </Form.Field>
          <Form.Button type="submit">sign up</Form.Button>
        </Form>
      </Segment>
      <Footer />
    </>
  );
};

const mapDispatchToProps = {
  createUser,
  login,
  setNotification
};

const CreateUser = withRouter(NewUser);

export default connect(null, mapDispatchToProps)(CreateUser);
