import React from "react";
import Footer from "./Footer";
import MenuNav from "./MenuNav";
import { Container, Header } from "semantic-ui-react";

const Affiliate = props => {
  return (
    <>
      <MenuNav />
      <Container textAlign="left" text style={{ marginTop: "1em" }}>
        <Header as="h1">Affiliate Disclosure</Header>
        <p>
          All Vintage Search, a subsdidiary of All Vintage Cards is an approved
          affiliate in the eBay Affiliate Partners program. If you click a link
          on our website and make a purchase on the eBay website, we might earn
          a small commission on the purchase. Note, the earnings help with
          development costs, so the more purchased, the more I can devote to
          improving the site with addtiional features! Thanks for helping out!
        </p>{" "}
      </Container>
      <Footer />
    </>
  );
};

export default Affiliate;
