import userService from "../services/user";
import { login } from "./loginReducer";

const userReducer = (state = [], action) => {
  switch (action.type) {
    case "NEW_USER":
      return [...state, action.data];
    case "ADD_FAVORITE":
      const userToChange = action.data.user;
      console.log("USER", userToChange);
      let favoritesToUpdate = userToChange.favorites;
      const changedUserFavorites = favoritesToUpdate.concat(action.data.card);
      userToChange.favorites = changedUserFavorites;
      userService.update(userToChange.id, action.data.card);
      return action.data.user;
    case "REMOVE_FAVORITE":
      const userToRemoveFav = action.data.user;
      userService.removeFav(userToRemoveFav.id, action.data.card);
      return action.data.user;
    case "DELETE_USER":
      const removeObject = action.data;
      return state.filter(user => user.id !== removeObject.id);
    case "INIT_USERS":
      return action.data;
    default:
      return state;
  }
};

export const initializeUsers = () => {
  return async dispatch => {
    const users = await userService.getAll();
    dispatch({
      type: "INIT_USERS",
      data: users
    });
  };
};

export const addFavorite = (card, user) => {
  return async dispatch => {
    dispatch({
      type: "ADD_FAVORITE",
      data: {
        card: card,
        user: user
      }
    });
  };
};

export const removeFavorite = (card, user) => {
  return async dispatch => {
    dispatch({
      type: "REMOVE_FAVORITE",
      data: {
        card: card,
        user: user
      }
    });
  };
};

export const createUser = content => {
  return async dispatch => {
    const newUser = await userService.create(content);
    dispatch({
      type: "NEW_USER",
      data: newUser
    });
    login(newUser);
  };
};

export const removeUser = userToDelete => {
  return async dispatch => {
    await userService.remove(userToDelete);
    dispatch({
      type: "DELETE_USER",
      data: userToDelete
    });
  };
};

export default userReducer;
