import React from "react";
import { connect } from "react-redux";
import {
  Form,
  Button,
  Grid,
  Header,
  Message,
  Segment
} from "semantic-ui-react";
import { Link, withRouter } from "react-router-dom";
import { login } from "../reducers/loginReducer";
import { setNotification } from "../reducers/notificationReducer";
import { useField } from "../hooks";
import { logout } from "../reducers/loginReducer";

const LoginForm = props => {
  const username = useField("text");
  const password = useField("password");

  const logout = () => {
    props.logout();
  };

  const handleLogin = async event => {
    event.preventDefault();
    try {
      const user = { username: username.value, password: password.value };
      await props.login(user);
      username.reset();
      password.reset();
      props.history.push("/");
    } catch (error) {
      props.setNotification("Sorry wrong username or password.", 5);
    }
  };

  if (props.loggedUser.id !== undefined)
    return (
      <p>
        You are currently logged in. Would you like to{" "}
        <Link onClick={logout} to="/">
          logout?
        </Link>
      </p>
    );

  return (
    <>
      <Grid centered columns={2}>
        <Grid.Column>
          <Header as="h2" textAlign="center">
            <Segment>
              <Link to="/">
                <img
                  src={`${process.env.PUBLIC_URL}/assets/images/all-vintage-search-logo.png`}
                  alt="All Vintage Search"
                  height="60px"
                />
              </Link>
            </Segment>
          </Header>
          <Segment>
            <Form onSubmit={handleLogin} size="large">
              <Form.Input
                fluid
                icon="user"
                iconPosition="left"
                placeholder="Username"
                id="username"
                {...username.omitreset}
              />
              <Form.Input
                fluid
                icon="lock"
                iconPosition="left"
                placeholder="Password"
                type="password"
                {...password.omitreset}
              />

              <Button color="blue" fluid size="large">
                Login
              </Button>
            </Form>
          </Segment>
          <Message>
            Not registered yet? <Link to="/newuser">Sign Up</Link>
          </Message>
        </Grid.Column>
      </Grid>
    </>
  );
};

const mapStateToProps = state => {
  return {
    notification: state.notification,
    loggedUser: state.loggedUser
  };
};

const mapDispatchToProps = {
  login,
  setNotification,
  logout
};

const Login = withRouter(LoginForm);

export default connect(mapStateToProps, mapDispatchToProps)(Login);
