import React from "react";
import { connect } from "react-redux";
import { Container, Header, Menu, Button, MenuItem } from "semantic-ui-react";
import { logout } from "../reducers/loginReducer";
import { Redirect } from "react-router-dom";
import { Link } from "react-router-dom";

const MenuNav = props => {
  const logout = () => {
    props.logout();
    return <Redirect to="/" />;
  };

  return (
    <Menu>
      <Container>
        <Menu.Item header>
          <Header as="h3">
            <Link to="/">
              <img
                src={`${process.env.PUBLIC_URL}/assets/images/all-vintage-search-logo.png`}
                alt="All Vintage Search"
                height="60px"
              />
            </Link>
          </Header>
        </Menu.Item>
        <Menu.Menu position="right">
          {props.loggedUser.id !== undefined ? (
            <>
              <Menu.Item link>
                <Link to="/favorites">My Favorites</Link>
              </Menu.Item>
              <Menu.Item link>
                <span>
                  <em>Hi {props.loggedUser.firstName} you're logged in!</em>{" "}
                  <Button size="mini" onClick={logout}>
                    logout
                  </Button>
                </span>
              </Menu.Item>
            </>
          ) : (
            <MenuItem>
              <Link to="/login">login</Link>
            </MenuItem>
          )}
        </Menu.Menu>
      </Container>
    </Menu>
  );
};

const mapStateToProps = state => {
  return {
    loggedUser: state.loggedUser
  };
};

const mapDispatchToProps = {
  logout
};

export default connect(mapStateToProps, mapDispatchToProps)(MenuNav);
